.App {
  display: flex;
  flex-direction: column; /* 垂直排列内容 */
  align-items: center; /* 水平居中 */
  justify-content: center; /* 垂直居中 */
  min-height: 100vh; /* 页面高度填满屏幕 */
  font-family: Arial, sans-serif;
}

.upload-container {
  text-align: center;
  margin-bottom: 40px;
}

.graph-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.legend {
  cursor: pointer;
}

.legend-label {
  font-size: 12px;
}

.tooltip {
  position: absolute;
  background: white;
  border: 1px solid #ccc;
  padding: 10px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}

h2 {
  margin: 0 0 20px -40px;
  font-size: 24px;
  font-weight: bold;
  color: #333;
  text-align:  left;
  width: 100%;
}